p-get($theme, foreground);\n\n    background-color: mat-color($background, background);\n    color: mat-color($foreground, text);\n  }\n\n  // Marker that is used to determine whether the user has added a theme to their page.\n  @at-root {\n    .mat-theme-loaded-marker {\n      display: none;\n    }\n  }\n}\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n@mixin mat-divider-theme($theme) {\n  $foreground: map-get($theme, foreground);\n\n  .mat-divider {\n    border-top-color: mat-color($foreground, divider);\n  }\n\n  .mat-divider-vertical {\n    border-right-color: mat-color($foreground, divider);\n  }\n}\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n// Create a theme.\n@mixin angular-material-theme($theme) {\n  @include mat-core-theme($theme);\n  @include mat-autocomplete-theme($theme);\n  @include mat-badge-theme($theme);\n  @include mat-bottom-sheet-theme($theme);\n  @include mat-button-theme($theme);\n  @include mat-button-toggle-theme($theme);\n  @include mat-card-theme($theme);\n  @include mat-checkbox-theme($theme);\n  @include mat-chips-theme($theme);\n  @include mat-table-theme($theme);\n  @include mat-datepicker-theme($theme);\n  @include mat-dialog-theme($theme);\n  @include mat-divider-theme($theme);\n  @include mat-expansion-panel-theme($theme);\n  @include mat-form-field-theme($theme);\n  @include mat-grid-list-theme($theme);\n  @include mat-icon-theme($theme);\n  @include mat-input-theme($theme);\n  @include mat-list-theme($theme);\n  @include mat-menu-theme($theme);\n  @include mat-paginator-theme($theme);\n  @include mat-progress-bar-theme($theme);\n  @include mat-progress-spinner-theme($theme);\n  @include mat-radio-theme($theme);\n  @include mat-select-theme($theme);\n  @include mat-sidenav-theme($theme);\n  @include mat-slide-toggle-theme($theme);\n  @include mat-slider-theme($theme);\n  @include mat-stepper-theme($theme);\n  @include mat-sort-theme($theme);\n  @include mat-tabs-theme($theme);\n  @include mat-toolbar-theme($theme);\n  @include mat-tooltip-theme($theme);\n  @include mat-tree-theme($theme);\n  @include mat-snack-bar-theme($theme);\n}\n","/*=======================*\\\n||  MDS Colours  ||\n\\*=======================*/\n\n$color-brand-electric-blue: #2251ff;\n$color-brand-deep-blue: #051c2c;\n$color-brand-cyan: #00a9f4;\n\n/*=======================*\\\n||  Application Colours  ||\n\\*=======================*/\n\n$font-dark: #000;\n$font-light: #fff;\n\n$success: #43a047;\n$success-light: lighten($success, 10);\n$success-dark: darken($success, 10);\n\n$pending: #ffb300;\n$pending-light: lighten($pending, 10);\n$pending-dark: darken($pending, 10);\n\n$error: #e53935;\n$error-light: lighten($error, 10);\n$error-dark: darken($error, 10);\n\n$color-primary: #1937ea;\n$color-primary-light: lighten($color-primary, 10);\n$color-primary-dark: darken($color-primary, 10);\n\n$color-secondary: #4285F4;\n$color-secondary-light: lighten($color-secondary, 10);\n$color-secondary-dark: darken($color-secondary, 10);\n\n$background: #f0f0f0;\n$footer-back: #263238;\n$header-back: linear-gradient(to right, #051C2C, #051C2C 40%, #0B2453 60%, #1F40E6 100%);\n\n$color-ternary: #051c2c;\n\n/*===========*\\\n||   Fonts   ||\n\\*===========*/\n\n$font-stack: \"Theinhardt\", \"Helvetica Neue\", Arial, sans-serif;\n\n$heading-font: \"LarishMcKinsey\", 'Georgia', serif;\n$font: $font-stack;\n\n$font-family-primary: \"McKinsey Sans\", \"Helvetica Neue\", Calibri, Corbel, Helvetica, Roboto, Droid, sans-serif;\n$font-family-secondary: \"Bower\", Georgia, \"Times New Roman\" serif;\n\n$base-size: 16px;\n$tablet-size: 16px;\n$mobile-size: 16px;\n\n/*===================*\\\n||   Media Queries   ||\n\\*===================*/\n\n$break-mobile: 450px;\n$break-tablet: 800px;\n$break-laptop: 1024px;\n\n$break-landscape-mobile: 800px;\n$break-landscape-tablet: 1048px;\n$break-landscape-laptop: 1280px;\n","@import './variables';\n\n@mixin hide-text-overflow {\n    white-space: nowrap;\n    overflow: hidden;\n    text-overflow: ellipsis;\n}\n\n@mixin box-shadow($color: #000, $depth: 1) {\n    box-shadow: 0 1px 3px 1px * ($depth - 1) rgba(#000, .2),\n                0 1px 1px 0 rgba(#000, .14),\n                0 2px 1px -1px rgba(#000, .12);\n}\n\n/*===================*\\\n||   Media Queries   ||\n\\*===================*/\n\n@mixin respond-to($media) {\n    @if $media == mobile {\n        @media only screen and (orientation: portrait) and (max-width: $break-mobile) {\n            @content;\n        }\n        @media only screen and (orientation: landscape) and (max-width: $break-landscape-mobile) {\n            @content;\n        }\n    } @else if $media == mobile-landscape {\n        @media only screen and (orientation: landscape) and (max-width: $break-landscape-mobile) {\n            @content;\n        }\n    } @else if $media == mobile-portrait {\n        @media only screen and (orientation: portrait) and (max-width: $break-mobile) {\n            @content;\n        }\n    } @else if $media == not-mobile {\n        @media only screen and (orientation: portrait) and (min-width: $break-mobile + 1) {\n            @content;\n        }\n        @media only screen and (orientation: landscape) and (min-width: $break-landscape-mobile + 1) {\n            @content;\n        }\n    } @else if $media == laptop {\n        @media only screen and (orientation: portrait) and (min-width: $break-tablet + 1) and (max-width: $break-laptop) {\n            @content;\n        }\n        @media only screen and (orientation: landscape) and (min-width: $break-landscape-tablet + 1) and (max-width: $break-landscape-laptop) {\n            @content;\n        }\n    } @else if $media == laptop-landscape {\n        @media only screen and (orientation: landscape) and (min-width: $break-landscape-tablet + 1) and (max-width: $break-landscape-laptop) {\n            @content;\n        }\n    } @else if $media == laptop-portrait {\n        @media only screen and (orientation: portrait) and (min-width: $break-tablet + 1) and (max-width: $break-laptop) {\n            @content;\n        }\n    }  @else if $media == lat {\n        @media only screen and (orientation: portrait) and (min-width: $break-mobile + 1) and (max-width: $break-tablet) {\n            @content;\n        }\n        @media only screen and (orientation: landscape) and (min-width: $break-landscape-mobile + 1) and (max-width: $break-landscape-tablet) {\n            @content;\n        }\n    } @else if $media == tablet {\n        @media only screen and (orientation: portrait) and (min-width: $break-mobile + 1) and (max-width: $break-tablet) {\n            @content;\n        }\n        @media only screen and (orientation: landscape) and (min-width: $break-landscape-mobile + 1) and (max-width: $break-landscape-tablet) {\n            @content;\n        }\n    } @else if $media == tablet-landscape {\n        @media only screen and (orientation: landscape) and (min-width: $break-landscape-mobile + 1) and (max-width: $break-landscape-tablet) {\n            @content;\n        }\n    } @else if $media == tablet-portrait {\n        @media only screen and (orientation: portrait) and (min-width: $break-mobile + 1) and (max-width: $break-tablet) {\n            @content;\n        }\n    } @else if ($media == tablet-mobile or $media == not-desktop) {\n        @media only screen and (orientation: portrait) and (max-width: $break-tablet) {\n            @content;\n        }\n        @media only screen and (orientation: landscape) and (max-width: $break-landscape-tablet) {\n            @content;\n        }\n    }@else if $media == tablet-landscape {\n        @media only screen and (orientation: landscape) and (min-width: $break-landscape-mobile + 1) and (max-width: $break-landscape-tablet) {\n            @content;\n        }\n    } @else if $media == tablet-portrait {\n        @media only screen and (orientation: portrait) and (min-width: $break-mobile + 1) and (max-width: $break-tablet) {\n            @content;\n        }\n    } @else if ($media == tablet-mobile or $media == not-desktop) {\n        @media only screen and (orientation: portrait) and (max-width: $break-tablet) {\n            @content;\n        }\n        @media only screen and (orientation: landscape) and (max-width: $break-landscape-tablet) {\n            @content;\n        }\n    } @else if $media == desktop {\n        @media only screen and (orientation: portrait) and (min-width: $break-tablet) {\n            @content;\n        }\n        @media only screen and (orientation: landscape) and (min-width: $break-landscape-tablet) {\n            @content;\n        }\n    } @else if $media == desktop-landscape {\n        @media only screen and (orientation: landscape) and (min-width: $break-landscape-tablet) {\n            @content;\n        }\n    } @else if $media == desktop-portrait {\n        @media only screen and (orientation: portrait) and (min-width: $break-tablet) {\n            @content;\n        }\n    } @else if $media == landscape {\n        @media only screen and (orientation: landscape) {\n            @content;\n        }\n    } @else if $media == portrait {\n        @media only screen and (orientation: portrait) {\n            @content;\n        }\n    }\n}\n","$primary: #1F40E6;\n$form-label: #051C2C;\n$secondary-label: #767676;\n$border: #B3B3B3;\n$background-checkbox-disabled: #D0D0D0;\n$chip-color: #333333;\n$placeholder-color: #7F7F7F;\n$background-color: #F0F0F0;\n$page-background-color: #F8F9FA;\n$selected-option: #E6E6E6;\n$color-brand-yellow: #ffd048;\n$light-yellow: #fff3ce;\n$coachmark-text: #6f4d00;\n$error-color: #E12E40;\n$no-filters-color: #BCBCBC;\n$results-list-background-color: #F4F4F4;\n$primary-highlight: #2251FF;\n$scrollbar-color: #757575;\n\n$available-color: #5D9D52;\n$available-by-request-color: #F5B53F;\n$in-use-color: #D4483E;\n$not-bookable-color: #CCCCCC;\n\n$modal-body-heading: #4D4D4D;\n$alert-overlay-bg: rgba(5, 28, 44, 0.85);\n\n$success: #43a047;\n$success-light: lighten($success, 10);\n$success-dark: darken($success, 10);\n\n$pending: #ffb300;\n$pending-light: lighten($pending, 10);\n$pending-dark: darken($pending, 10);\n\n$error: #e53935;\n$error-light: lighten($error, 10);\n$error-dark: darken($error, 10);\n\n$coachmark-bg: #EBEBF6;\n$coachmark-dark-text: #1B1B19;\n\n/**\n * Spacing\n */\n\n// 4px\n$spacing-smallest: 0.25rem;\n\n// 8px\n$spacing-xxsmall: 0.5rem;\n\n// 12px\n$spacing-xsmall: 0.75rem;\n\n// 16px\n$spacing-small: 1rem;\n\n// 20px\n$spacing-small-medium: 1.25rem;\n\n// 24px\n$spacing-medium: 1.5rem;\n\n// 28px\n$spacing-medium-large: 1.75rem;\n\n// 32px\n$spacing-large: 2rem;\n\n// 48px\n$spacing-xlarge: 3rem;\n\n// 64px\n$spacing-xxlarge: 4rem;\n\n// 96px\n$spacing-largest: 6rem;\n\n/*===================*\\\n||   Media Queries   ||\n\\*===================*/\n\n$device-mobile-size: 450px;\n$device-tablet-size: 800px;\n$device-laptop-size: 1024px;\n\n$device-landscape-mobile-size: 800px;\n$device-landscape-tablet-size: 1048px;\n$device-landscape-laptop-size: 1280px;\n\n\n:export {\n    primary: $primary;\n    secondaryLabel: $secondary-label;\n}\n\n/*===================*\\\n||   Icon sizes  ||\n\\*===================*/\n$icon-small: 16px;\n$icon-medium: 24px;\n$icon-large: 32px;\n$icon-xlarge: 48px;\n","@import \"vars\";\n\n@mixin query-select($orientation, $check-orientation, $size, $qualifier) {\n    @if $orientation == none or $orientation == $check-orientation {\n        @if ($qualifier == min-width) {\n            @media only screen and (orientation: $check-orientation) and (min-width: $size) {\n                @content;\n            }\n        } @else if ($qualifier == max-width) {\n            @media only screen and (orientation: $check-orientation) and (max-width: $size) {\n                @content;\n            }\n        }\n    }\n}\n\n@mixin bounded-select($orientation, $check-orientation, $min, $max) {\n    @if $orientation == none or $orientation == $check-orientation {\n        @media only screen and (orientation: $check-orientation) and (min-width: $min) and (max-width: $max) {\n            @content;\n        }\n    }\n}\n\n// this is a copy of respond-to. we don't want to preserve that method since the breakpoints may diverge from the old UI.\n@mixin is-screen-size($media: all, $orientation: none) {\n    @if $media == mobile {\n        @include query-select($orientation, portrait, $device-mobile-size, max-width) {\n            @content;\n        }\n        @include query-select($orientation, landscape, $device-landscape-mobile-size, max-width) {\n            @content;\n        }\n    } @else if $media == not-mobile {\n        @include query-select($orientation, portrait, $device-mobile-size + 1, min-width) {\n            @content;\n        }\n        @include query-select($orientation, landscape, $device-landscape-mobile-size + 1, min-width) {\n            @content;\n        }\n    } @else if $media == laptop {\n        @include bounded-select($orientation, portrait, $device-tablet-size + 1, $device-laptop-size) {\n            @content;\n        }\n        @include bounded-select($orientation, landscape, $device-landscape-tablet-size + 1, $device-landscape-laptop-size) {\n            @content;\n        }\n    } @else if $media == tablet {\n        @include bounded-select($orientation, portrait, $device-mobile-size + 1, $device-tablet-size) {\n            @content;\n        }\n        @include bounded-select($orientation, landscape, $device-landscape-mobile-size + 1, $device-landscape-tablet-size) {\n            @content;\n        }\n    } @else if $media == desktop {\n        @include query-select($orientation, portrait, $device-tablet-size, min-width) {\n            @content;\n        }\n        @include query-select($orientation, landscape, $device-landscape-tablet-size, min-width) {\n            @content;\n        }\n    } @else if $orientation == landscape {\n        @media only screen and (orientation: landscape) {\n            @content;\n        }\n    } @else if $orientation == portrait {\n        @media only screen and (orientation: portrait) {\n            @content;\n        }\n    }\n}\n\n\n@mixin hide-text-overflow {\n    white-space: nowrap;\n    overflow: hidden;\n    text-overflow: ellipsis;\n}\n\n\n@mixin page-padding {\n    padding-left: $spacing-small;\n    padding-right: $spacing-small;\n}\n\n// Font Mixins\n$font-mckinsey-sans: \"McKinsey Sans\";\n$font-theinhardt: \"Theinhardt\", \"Helvetica Neue\", Arial, sans-serif;\n$font-bower: \"Bower\";\n\n@mixin bower-bold {\n    font-family: $font-bower;\n    font-style: normal;\n    font-weight: bold;\n    text-align: center;\n}\n\n@mixin mckinsey-sans {\n    font-family: $font-mckinsey-sans;\n    font-style: normal;\n    text-align: center;\n}\n\n@mixin display-1 {\n    @include bower-bold;\n    font-size: 3.75rem; // 60px\n    line-height: 1.2; // 72px\n\n    @include is-screen-size(not-mobile) {\n        font-size: 4rem; // 64px\n        line-height: 1.187; // 76px\n    }\n}\n\n@mixin display-2 {\n    @include bower-bold;\n    font-size: 3.5rem; // 56px\n    line-height: 1.214; // 68px\n\n    @include is-screen-size(not-mobile) {\n        font-size: 3.75rem; // 60px\n        line-height: 1.2; // 72px\n    }\n}\n\n@mixin display-3 {\n    @include bower-bold;\n    font-size: 3.25rem; // 52px\n    line-height: 1.153; // 60px\n\n    @include is-screen-size(not-mobile) {\n        font-size: 3.5rem; // 56px\n        line-height: 1.214; // 68px\n    }\n}\n\n@mixin display-4 {\n    @include bower-bold;\n    font-size: 3rem; // 48px\n    line-height: 1.166; // 56px\n\n    @include is-screen-size(not-mobile) {\n        font-size: 3.25rem; // 52px\n        line-height: 1.153; // 64px\n    }\n}\n\n@mixin display-5 {\n    @include bower-bold;\n    font-size: 2rem; // 30px\n    line-height: 1.833; // 34px\n\n    @include is-screen-size(not-mobile) {\n        font-size: 2.25rem; // 36px\n        line-height: 1.642; // 40px\n    }\n}\n\n@mixin display-7 {\n    @include bower-bold;\n    font-size: 1.5rem; // 24px\n    line-height: 1.333; // 28px\n\n    @include is-screen-size(not-mobile) {\n        font-size: 1.75rem; // 28px\n        line-height: 1.142; // 32px\n    }\n}\n\n@mixin heading-1 {\n    @include mckinsey-sans;\n    font-size: 2.5rem; // 40px\n    line-height: 1.2; // 48px\n\n    @include is-screen-size(not-mobile) {\n        font-size: 2.75rem; // 44px\n        line-height: 1.181; // 52px\n    }\n}\n\n@mixin heading-2 {\n    @include mckinsey-sans;\n    font-size: 2.25rem; // 36px\n    line-height: 1.111; // 44px\n\n    @include is-screen-size(not-mobile) {\n        font-size: 2.5rem; // 40px\n        line-height: 1.181; // 48px\n    }\n}\n\n@mixin heading-3 {\n    @include mckinsey-sans;\n    font-size: 2rem; // 32px\n    line-height: 1.125; // 36px\n\n    @include is-screen-size(not-mobile) {\n        font-size: 2.25rem; // 36px\n        line-height: 1.222; // 44px\n    }\n}\n\n@mixin heading-4 {\n    @include mckinsey-sans;\n    font-size: 1.75rem; // 28px\n    line-height: 1.142; // 32px\n\n    @include is-screen-size(not-mobile) {\n        font-size: 2rem; // 32px\n        line-height: 1.125; // 36px\n    }\n}\n\n@mixin heading-5 {\n    @include mckinsey-sans;\n    font-size: 1.5rem; // 24px\n    line-height: 1.333; // 288px\n\n    @include is-screen-size(not-mobile) {\n        font-size: 1.75rem; // 28px\n        line-height: 1.142; // 32px\n    }\n}\n\n@mixin heading-6 {\n    @include mckinsey-sans;\n    font-size: 1.25rem; // 20px\n    line-height: 1.4; // 24px\n\n    @include is-screen-size(not-mobile) {\n        font-size: 1.5rem; // 24px\n        line-height: 1.155; // 28px\n    }\n}\n\n@mixin body-default {\n    @include mckinsey-sans;\n    font-size: 1rem; // 16px\n    line-height: 1.4; // 24px\n\n    @include is-screen-size(not-mobile) {\n        font-size: 1.25rem; // 20px\n        line-height: 1.6; // 26px\n    }\n}\n\n/// Doesnt exist in MDS.\n@mixin body-tiny {\n    @include mckinsey-sans;\n    font-size: 0.75rem; // 12px\n    line-height: 1.4;